main[role="main"] {}

#main-content {
  min-height: 100%;
}

#main-filter {
  height: 100px;
  padding: 20px;
}

#main-content-left {
  width: 60%;
}
#location {
  font-size: 10px;
}
#expert {
  margin-top: 20px;
}

#main-content-right {
  width: 40%;
}

#main-filter,
#main-content-left,
#main-content-right-top,
#main-content-right-bottom-left,
#main-content-right-bottom-right {
  background: var(--background-primary);
  border-radius: 10px;
}

#main-content-right-top,
#main-content-right-bottom {
  height: 50%;
}

#main-content-right-bottom-left,
#main-content-right-bottom-right {
  width: 50%;
}

#main-content-left {
  padding: 20px;
}
#main-content-left p {
  width: 200px;
  color: var(--text-tertiary);
}

#experience {
  position: absolute;
  left: 30px;
  bottom: 30px;
}

.contact-link {
  text-decoration: none;
}
.contact-link svg {
  fill: var(--text-tertiary);
}
#carousel-item-1-content {
  padding: 20px 0px 10px 0px;
}

.carousel-label {
  background-color: var(--text-primary);
  color: var(--background-primary);
  padding: 4px 10px;
  white-space: nowrap;
  border-radius: 6px;
  margin-top: 30px;
  font-size: 14px;
}
.hobbies-label {
  padding: 4px 28px;
}
.hobbies {
  padding-top: 8px;
  color: var(--text-tertiary);
  font-size: 14px;
  list-style: none;
}
#main-content-right-bottom-right.gradient-blue {
  background: #0052d4; 
  background: -webkit-linear-gradient(to top, #0052d4, #4364f7, #6fb1fc); 
  background: linear-gradient(to top, #0052d4, #4364f7, #6fb1fc); 
}
#main-content-right-bottom-right.gradient-light-blue {
  background: #2980b9; 
  background: -webkit-linear-gradient(to top, #2980b9, #6dd5fa, #ffffff); 
  background: linear-gradient(to top, #2980b9, #6dd5fa, #ffffff); 
}
#main-content-right-bottom-right.gradient-animated {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.history {
  color: white; 
  padding: 20px;
}

huanlin-history p {
  padding-top: 10px;
  font-size: 14px;
}

#main-content-right-bottom-left {
  padding: 20px;
}
#main-content-right-bottom-left p {
  padding-top: 10px;
  color: var(--text-tertiary);
}

@media only screen and (max-width: 800px) {
  #main-filter {
    justify-content: center;
  }
  #banner {
    padding: 10px;
  }
  #main-content, #main-content-right-bottom {
    flex-direction: column;
  }
  #main-content-left, #main-content-right, #main-content-right-bottom-left, #main-content-right-bottom-right {
    width: 100%;
  }
  #main-content-right-bottom-left, #main-content-right-bottom-right {
    height: 300px;
  }
  #main-content-left {
    height: 600px;
  }
  #main-content-left > div:nth-child(2) {
    display: flex;
    justify-content: center;
  }
  #banner-center {
    display: none;
  }
  #open-navigation-button, #main-filter huanlin-select:nth-child(2) {
    display: none;
  }
}

.conatct-info {
  display: flex;
  height: 50vh;
  align-items: center;
  justify-content: center;
}
.mortgage {
  display: flex;
  flex: 1;
}
.card {
  border-radius: 10px;
  padding: 20px;
  width: 100%;
  box-sizing: border-box;
}

#tooltip {
  width: fit-content;
  height: fit-content;
  position: fixed;
  display: none;
  flex-direction: column;
  background-color: var(--background-primary);
  padding: 8px;
  border-radius: 4px;
  box-shadow: var(--popover-shadow);
  user-select: none;
  // transition: opacity .05s ease-in-out;
  .content {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .dot {
    margin-right: 10px;
    min-width: 10px;
    min-height: 10px;
  }
  .year {
    font-weight: bold;
    font-size: 16px;
  }
  .text {
    font-size: 14px;
  }
}
#tooltip.donut .dot {
  border-radius: 50%;
}