* {
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
}

.position-relative {
  position: relative;
}

.flex {
  display: flex;
}

.justify-end {
  justify-content: flex-end;
}

.fit-content {
  width: fit-content;
}

.align-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.flex-row {
  flex-direction: row;
}

.flex-column {
  flex-direction: column;
}

.flex-grow-1 {
  flex-grow: 1;
}

.display-block {
  display: block;
}

.justify-space-between {
  justify-content: space-between;
}

.gap {
  gap: 10px;
}

.width-100p, .w-100p {
  width: 100%;
}
.h-100p {
  height: 100%;
}

.min-height-100p {
  min-height: 100%;
}

.height-100p {
  height: 100%;
}

.text-primary {
  color: var(--text-primary);
}

.text-secondary {
  color: var(--text-secondary);
}

.text-tertiary {
  color: var(--text-tertiary);
}

.subtitle {
  display: block;
  width: fit-content;
  font-size: 16px;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

huanlin-select, huanlin-carousel-item, huanlin-carousel, huanlin-clock, huanlin-live-editor, huanlin-mortgage, huanlin-home,
huanlin-contact, huanlin-donut, huanlin-stacked-bar-chart, huanlin-group-bar-chart, huanlin-line-chart, huanlin-pie-chart,
