@import "./app-header.scss";
@import "./app-navigation.scss";
@import "./app-main.scss";
@import "./app-global.module.scss";

body {
  --font-family: 'Inter', sans-serif;
  --font-family-strong: 'Roboto', sans-serif;
  --font-family-secondary: 'Lato', sans-serif;
  --background-primary: white;
  --background-secondary: #f2f2f2;
  --text-primary: black;
  --text-secondary: #333030;
  --text-tertiary: #545454;
  --text-quaternary: #c0bdbd;
  --border-color: #d4d8e0;
  --text-light: #9eb3d0;
  --color-primary: #1e85da;
  --color-tertiary: #e5e9f1;
  --color-primary-hover: #0a61a8;
  --color-primary-active: #042845;
  --popover-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  --header-bg: #f6f6f9;
  --divider-color: #edeef4;
}

#app {
  background: var(--background-secondary);
  color: var(--text-primary);
  left: 0;
  min-height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  padding: 0px 10px 10px 10px;
}

#app.dark {
  --background-primary: black;
  --background-secondary: #191b33;
  --divider-color: #191b33;
  --header-bg: #191b33;
  --text-primary: white;
  --text-secondary: #f2ebeb;
  /* --text-secondary: #333030; */
  --text-tertiary: #f2f2f2;
  --color-tertiary: #082b72;
}