nav[role="navigation"] {
  position: fixed;
  z-index: 1;
  top: -120px;
  right: 0;
  bottom: -120px;
  left: 0;
  visibility: hidden;
  transition-property: visibility;
}

#scrim {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition-property: opacity;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  opacity: 0;
  background: var(--app-drawer-scrim-background, rgba(0, 0, 0, 0.5));
}